import React from 'react'
import {Segment, Button, Divider,Tab,Input,Grid,Menu } from 'semantic-ui-react'


function Wrapper(props) {
    console.log(props)
    const { activeItem , menuItemClick ,onChange, onKeyPress,value,segmentStyle} = props
    return (
        <Segment style={segmentStyle} textAlign='center'>
            <Grid style={{padding:'20px 0 20px 0'}}>
                <Menu position='right' secondary>
                    <Menu.Item name='Latest' active={activeItem === 1} onClick={menuItemClick} />
                    <Menu.Item
                        name='A-Z'
                        active={activeItem === 2}
                        onClick={menuItemClick}
                    />
                    <Menu.Item
                        name='Most'
                        active={activeItem === 3}
                        onClick={menuItemClick}
                    />
                </Menu>
                <Grid.Column floated='right' mobile={16} tablet={8} computer={6}>
                    <Input 
                        style={{width:'100%'}} 
                        icon='search' 
                        placeholder='Search...'
                        value={value}
                        onChange={onChange} 
                        onKeyPress={onKeyPress}/>
                </Grid.Column>  
            </Grid>



            <Divider />
            {props.children}
        </Segment>
    )
}

export default Wrapper
